    font-size: 1em;
    font-weight: 400;
    padding: 16px 10px 6px 10px;
    margin: 0px 0px 10px 0px;
    border-right: solid black 1px;
}

.dashboard-head p {
    width: 70%;
    background-color: whitesmoke;
    padding-top: 16px;
    padding-left: 19px;
    margin-top: 0px;
}